@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap');
.logo-nav-bar {
    position: absolute;
}

.sign-up-btn {
    top: 25px;
    width: 10vw;
    height: 6vh;
    border-color: #FF61AB;
    color: #FF61AB;
    font: normal normal Poppins;
    background-color: white;
    opacity: 1;
    border-radius: 25px;
    margin-left: 3vh;
    font-weight: bold;
}

.sign-in {
    border: none;
    background-color: white;
    color: #FF61AB;
    font-weight: bolder;
}

.sign-up-btn-nav,
.sign-in-nav {
    background: none;
    color: #FF61AB;
    font: normal normal Poppins;
    border: none;
    font-weight: bolder;
}

.start-know-me {
    position: relative;
    overflow-y: hidden;
    overflow-x: hidden;
    height: 100vh;
}

.logo-and-text {
    top: 25vh;
    left: 15vw;
}

.logo-know-me {
    height: 5.3276em;
    background: transparent 0% 0% no-repeat padding-box;
    opacity: 1;
    background-size: contain;
    float: left;
    /* top:42vh; */
    /* height: 15vh; */
    /* לפתןר את בעיית הגובה */
}

.describe-know-me {
    float: left;
}

.describe-know-me-1 {
    font: normal normal xx-large Poppins;
    letter-spacing: 1.12px;
    color: #FDFDFD;
    opacity: 1;
    left: 5em;
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    text-align: left;
}

.describe-know-me-2 {
    font-size: medium;
    font-family: 'Poppins', sans-serif;
    color: #FDFDFD;
    text-align: left;
}

.think-big {
    margin-top: 10vh;
}

.think-it-about {
    text-align: left;
}

.think-it-about>p {
    margin-bottom: 3px;
    color: #505F98;
    text-align: left;
    font: normal normal normal 20px/24px Roboto;
    letter-spacing: 0.24px;
    opacity: 1;
    font-size: medium;
}

.think-it-h1 {
    text-align: left;
    font: normal normal medium 50px/66px Roboto;
    letter-spacing: 1px;
    color: black;
    opacity: 1;
}

.lets-start-btn {
    box-shadow: 0px 0px 10px white;
    background-color: white;
    border: none;
    opacity: 1;
    border-radius: 25px;
    float: left;
}

.lets-start-text {
    padding-top: 0.9em;
    padding-right: 0.9em;
    padding-left: 0.9em;
    font-size: large;
    font-family: 'Roboto', sans-serif;
    color: #E10D7A;
    text-align: center;
    font: normal normal medium Roboto;
    letter-spacing: 0.46px;
    text-transform: capitalize;
    opacity: 1;
}

.triangular {
    padding-right: 0.9em;
    color: #E10D7A;
    width: 0;
    height: 0;
    opacity: 1;
    border-top: 13px solid transparent;
    border-left: 17px solid;
    border-bottom: 13px solid transparent;
    margin: auto;
}

.think-it-background {
    width: 100vw;
    height: 45vh;
    background: transparent 0% 0% no-repeat padding-box;
    opacity: 1;
    background-position: center;
    background-size: contain;
    position: relative;
}

.background-img {
    position: absolute;
    width: 100vw;
    height: 100vh;
    left: 0;
    background: transparent url('../../imgs/part-0.png') no-repeat;
    background-position: center;
    background-size: cover;
}

.img {
    position: absolute;
}

.comp {
    position: relative;
    margin-top: 100px;
}

.phone-img-1 {
    position: relative;
    animation: mymove 5s infinite;
    animation-delay: 1s;
    animation-duration: 3s;
    margin-top: 10vh;
    left: 26vw;
    /* width:30vw; */
    height: 90vh;
    background: transparent 0% 0% no-repeat padding-box;
    opacity: 1;
    background-size: contain;
    border: none;
    overflow-y: hidden;
}

.phone-img-2 {
    top: 37vh;
    left: 15vh;
    height: 56vh;
    background: transparent 0% 0% no-repeat padding-box;
    opacity: 1;
    position: absolute;
}

.circle {
    position: absolute;
    width: 21px;
    height: 21px;
    border: 5px solid var(--unnamed-color-fdcb20);
    border: 5px solid #FDCB20;
    border-radius: 50%;
    opacity: 1;
}

.x {
    position: absolute;
    top: 30vh;
    left: 90vw;
    width: 21px;
    height: 21px;
    transform: rotate(20deg);
    opacity: 1;
}

.circle-1 {
    top: 31vh;
    left: 62vw;
}

.circle-2 {
    top: 59vh;
    left: 87vw;
}

.aplicate {
    top: 35vh;
    left: 75vw;
    position: absolute;
}

.aplicate1 {
    top: 41vh;
    left: 26vw;
    position: absolute;
}

.taltal {
    top: 80vh;
    left: 93vw;
    position: absolute;
}

@media screen and (max-width: 415px) {
    .background-img {
        height: 93vh;
    }
    .navbar {
        height: 7vh;
    }
    .logo-and-text {
        margin-top: 4vh;
    }
    .describe-know-me {
        float: unset;
    }
    .phone-img-2 {
        position: absolute;
        background-size: contain;
    }
    .lets-start-btn {
        z-index: 4;
        background-color: #FDCB20;
        border: none;
        position: fixed;
        margin-top: 50vh;
        left: 13vw;
        /* margin-left: 5vw;  */
    }
    .triangular {
        color: #FDFDFD;
    }
    .lets-start-text {
        color: white;
    }
    .logo-and-text {
        top: 3vh;
        left: auto;
    }
    .describe-know-me-1 {
        text-align: center;
        margin: auto;
        line-height: 130%;
    }
    .describe-know-me-2 {
        text-align: center;
        margin-left: auto;
    }
    .taltal {
        margin-top: -40vh;
        margin-left: -15vw;
    }
    .logo-know-me {
        float: unset;
        width: 80vw;
        height: 17vw;
    }
    .sign-up-btn {
        color: #FF61AB;
        font: normal normal Poppins;
        background-color: white;
        opacity: 1;
        font-weight: bold;
        border: none;
    }
    .sign-in {
        border: none;
        background-color: white;
        color: #FF61AB;
        font-weight: bolder;
    }
}

@keyframes mymove {
    40% {
        transform: rotate(10deg);
    }
}