@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap');
body {
    overflow-x: hidden;
}

.App {
    text-align: center;
    height: 100vh;
    overflow-x: hidden;
}

.comp-5 {
    width: 90%;
    margin-top: 10vh;
}

.wraps {
    z-index: 1;
}

@media screen and (max-width:414px),
screen and (max-height: 812px) {
    .aa .comp-1 {
        margin-top: 20vh;
    }
    .part-3 .title-2 {
        margin-left: 5vw;
    }
    .shapes-mobile img {
        position: absolute;
        right: 0;
    }
    .comp-mobile .mp3 {
        width: 75vw;
    }
    .p-2 {
        top: 40vh;
    }
    .p-3 {
        top: 20vh;
    }
    .p-4 {
        top: 30vh;
        margin-left: 5vw;
    }
    .empty {
        height: 25%;
    }
    .empty-2 {
        height: 2%;
    }
    .part-2,
    .part-3,
    .part-4 {
        overflow-y: hidden;
        height: 200vh;
        background-repeat: no-repeat;
        background-size: cover;
    }
    .part-2 {
        background-image: url('../../imgs/part-1.png');
    }
    .part-3 {
        background-image: url('../../imgs/part-2.png');
    }
    .part-4 {
        background-image: url('../../imgs/part-3.png');
        overflow-x: hidden;
    }
    .part-5 {
        height: 100vh;
    }
    .comp-1 {
        width: 100%;
    }
    .comp-2 {
        top: 3vh;
    }
    .title-1 {
        margin: auto;
        /* text-align: center; */
        margin-top: 3vh;
        margin-bottom: 1vh;
    }
    .text-1 {
        height: 0;
    }
    .text-1>li {
        /* margin-left:25px ; */
        margin-bottom: 2vh;
    }
    .text-3 {
        margin-left: 5vw;
        margin-right: 5vw;
    }
    .part-3 .text-3 {
        margin-left: 5vw;
        margin-right: 5vw;
    }
    .comp-mobile {
        height: 0;
    }
}

@media screen and (min-width: 1024px),
screen and (min-height: 824px) {
    .aa .comp-1 {
        margin-top: 15vh;
    }
    .part-3 .text-1 {
        margin-top: 15vh;
    }
    .p2 .title-2 {
        float: left;
    }
    .shapes-desk img {
        position: absolute;
        margin-top: 35vh;
        width: 75%;
    }
    .part-2 .shapes-desk img {
        right: 0;
    }
    .mp3 {
        /* width: 20vw; */
        height: 50vh;
    }
    .aa {
        height: 0;
    }
    .comp-2 {
        height: 45vh;
    }
    .comp-3 {
        top: 25vh;
    }
    .p-2 {
        top: 40vh;
    }
    .part-2 {
        background-image: url('../../imgs/desk-part-2.png');
        height: 120vh;
    }
    .part-3 {
        background-image: url('../../imgs/desk-part-3.png');
        height: 150vh;
    }
    .part-4 {
        background-image: url('../../imgs/desk-part-4.png');
        height: 130vh;
    }
    .part-2,
    .part-3,
    .part-4 {
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        overflow-x: hidden;
    }
    .part-5 {
        height: 50vh;
    }
    .text-1.part-3 {
        margin-top: 25vh;
    }
    .title-1 {
        font-size: 150%;
        text-align: left;
        margin-top: 7vh;
        margin-bottom: 3vh;
    }
    .text-1>li {
        margin-bottom: 1vh;
        font-size: medium;
    }
    .text-1>p {
        font-size: medium;
    }
    .create-card-btn-pink {
        margin-top: 2vh;
        background-color: #E10D7A;
        height: 6vh;
        /* width: 18vw; */
        color: white;
        font-size: 80%;
        border-radius: 29px;
        border: none;
        opacity: 1;
    }
    .create-card-btn-yellow {
        background-color: rgb(221, 236, 12);
        height: 6vh;
        /* width: 18vw; */
        color: white;
        font-size: 90%;
        border-radius: 29px;
        border: none;
        opacity: 1;
        float: left;
    }
    .comp-1 {
        margin-top: 2vh;
    }
    .comp-4 {
        margin-top: 15vh;
        /* width: 35vw;
      height: 45vh; */
    }
    .p-3 {
        margin-top: 15vh;
    }
    .comp-3 {}
}


/* another */

.title-1 {
    font: normal normal medium 50px/66px Roboto;
    letter-spacing: 1px;
    color: black;
    opacity: 1;
    font-weight: bolder;
}

.title-2 {
    color: var(--unnamed-color-ffffff);
    text-align: left;
    font: normal normal bold 24px/26px Roboto;
    font-size: 160%;
    letter-spacing: 0.36px;
    color: white;
    text-transform: capitalize;
    opacity: 1;
}

.text-1 {
    color: #505F98;
    text-align: left;
    font: normal normal normal 20px/24px Roboto;
    letter-spacing: 0.24px;
    opacity: 1;
    font-size: larger;
}

.text-2 {
    color: var(--unnamed-color-ffffff);
    text-align: left;
    font: normal normal normal 18px/26px Roboto;
    letter-spacing: 0.14px;
    color: white;
    text-transform: capitalize;
    opacity: 1;
}

.mp3 {
    width: 100%;
}

.terms-of-use-button {
    /* position: absolute; */
    width: 100%;
    height: 50px;
    margin-top: calc(30vh - 50px);
    right: 0;
    text-align: start;
    box-shadow: 0px 0px 10px white;
    background-color: #8D8D8D19;
    color: #75798E;
    opacity: 1;
    border: 1px solid #70707055;
    font: normal normal normal 14px/54px Roboto;
    letter-spacing: 0px;
    color: #75798E;
    text-transform: lowercase;
}


/* calc(50% - 100px) */